.portfolioBlock {
    min-height: 100vh;
    background-color: lightblue;
}

.portfolioContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.portfolio {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.title {
    background-color: yellow;
}